.vue-switcher {
  position: relative;
  display: inline-block;
}
.vue-switcher__label {
    display: block;
    font-size: 10px;
    margin-bottom: 5px;
}
.vue-switcher input {
    opacity: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 1;
    cursor: pointer;
}
.vue-switcher div {
    height: 15px;
    width: 36px;
    position: relative;
    border-radius: 30px;
    display: -ms-flex;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    cursor: pointer;
    -webkit-transition: linear .2s, background-color linear .2s;
    transition: linear .2s, background-color linear .2s;
}
.vue-switcher div:after {
      content: '';
      height: 20px;
      width: 20px;
      border-radius: 100px;
      display: block;
      -webkit-transition: linear .15s, background-color linear .15s;
      transition: linear .15s, background-color linear .15s;
      position: absolute;
      left: 100%;
      margin-left: -18px;
      cursor: pointer;
      top: -3px;
      -webkit-box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.1);
              box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.1);
}
